* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #2d384d;
  text-align: center;
}

.typeLink {
  text-decoration: none;
  text-align: center;
}

.back {
  padding: 10px;
  background-color: green;
  color: white;
  margin-top: 10px;
}

.back:hover {
  color: black;
  background-color: #eee;
}

h3 {
  width: 100%;
  margin: 20px;
}

.typesContainer {
  display: flex;
  justify-content: center;
}

.navTypes {
  margin-top: 20px;
  height: fit-content;
  padding: 25px;
  background-color: #303e53;
  width: 50%;
  color: #eee;
}

.navTypes > .navText {
  font-size: 5.5rem;
}

.pokeTypes {
  margin: 20px 0 0 15px;
  height: fit-content;
  color: white;
  padding: 25px;
  background-color: #303e53;
  width: 25%;
}

.homeImage {
  width: 350px;
}

.pickachuOutline {
  width: 200px;
}

@media only screen and (min-width: 319px) and (max-width: 767px) {
  .navTypes {
    margin-top: 20px;
    height: fit-content;
    background-color: #303e53;
    width: 100%;
    color: #eee;
  }

  .pokeTypes {
    height: fit-content;
    margin: 10px 0;
    color: white;
    background-color: #303e53;
    width: 100%;
  }

  .headerDiv {
    display: flex;
    justify-content: center;
    background-color: #eee;
    padding: 15px 20px;
    margin: 0;
  }

  .homeImage {
    width: 50%;
  }

  .pickachuOutline {
    width: 30%;
  }

  .navTypes > .navText {
    font-size: 2rem;
  }

  .typesContainer {
    display: flex;
    flex-wrap: wrap-reverse;
  }

  .pokemonInfoImg {
    margin-top: 20px;
    width: 100px;
  }

  .right,
  .left {
    width: 100%;
    padding: 0;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .navTypes {
    margin-top: 20px;
    height: fit-content;
    background-color: #303e53;
    width: 100%;
    color: #eee;
  }

  .pokeTypes {
    height: fit-content;
    margin: 10px 0;
    color: white;
    background-color: #303e53;
    width: 100%;
  }

  .headerDiv {
    display: flex;
    justify-content: center;
    background-color: #eee;
    padding: 15px 20px;
    margin: 0;
  }

  .homeImage {
    width: 50%;
  }

  .pickachuOutline {
    width: 30%;
  }

  .navTypes > .navText {
    font-size: 2rem;
  }

  .typesContainer {
    display: flex;
    flex-wrap: wrap-reverse;
  }
}

@media only screen and (min-width: 1024px) and (max-width: 1365px) {
  .navTypes {
    margin: 20px;
    height: fit-content;
    background-color: #303e53;
    width: 100%;
    color: #eee;
  }

  .pokeTypes {
    height: fit-content;
    margin: 10px 10px;
    color: white;
    background-color: #303e53;
    width: 50%;
  }
}
